import { useBlockProps, BlockControls, AlignmentToolbar } from "@wordpress/block-editor";
import { useEffect } from "@wordpress/element";
import "../styles/editor.scss";
import { __ } from "@wordpress/i18n";
import { prefix } from "../../../constants";
import ServerSideRender from "@wordpress/server-side-render";
import InspectorPanel from "./inspector";
import { Spinner } from "@wordpress/components";
import { getTypoStyle, getDimensionStyle } from "../../../helpers";

const Edit = (props) => {
	const {
		attributes: {
			titleTypo,
			priceTypo,
			buttonTypo,
			layout,
			buttonPadding,
			contentPadding,
			titlePadding,
			padding,
			margin,
			alignment,
			bgColor,
			removeColor,
			removeBGColor,
			buttonBGColor,
			buttonColor
		},
		setAttributes,
	} = props;

	useEffect(() => {
		setAttributes({
			block_id: `${prefix}-wishlist-table-block-instance-${blockProps.id}`,
		});
	});

	const blockProps = useBlockProps({
		className: `${prefix}-wrapper`,
	});

	const containerStyle = {
		...getDimensionStyle("padding", padding),
		...getDimensionStyle("margin", margin),
	};

	const titleStyle = {
		...getTypoStyle(titleTypo),
		...getDimensionStyle("padding", titlePadding),
	};

	const buttonStyle = {
		...getDimensionStyle("padding", buttonPadding),
		...getTypoStyle(buttonTypo),
		backgroundColor: buttonBGColor,
		color: buttonColor
	};

	const priceStyle = {
		...getTypoStyle(priceTypo),
	};

	const contentStyle = {
		...getDimensionStyle(["paddingRight", "paddingLeft"], contentPadding),
	};

	const objectToCssString = (obj) =>
		Object.keys(obj)
			.map(
				(key) =>
					`${key.replace(/([A-Z])/g, "-$1").toLowerCase()}: ${obj[key]};`,
			)
			.join("");

	return (
		<>
			<style>
				{`#${blockProps.id} .wc-booster-wishlist-page{
					${objectToCssString(containerStyle)}
				}`}
				{`#${blockProps.id} .wc-booster-alignment-center{
					text-align: ${alignment}
				}`}
				{`#${blockProps.id} .grid-layout .wc-booster-product-block{
					background-color: ${bgColor}
				}`}
				{`#${blockProps.id} .grid-layout button.product-remove-btn{
					background-color: ${removeBGColor};
					color: ${removeColor};
				}`}
				{`#${blockProps.id} .product-contents a,
				  #${blockProps.id} .product-title-img a{
					${objectToCssString(titleStyle)}
				}`}
				{`#${blockProps.id} .product-contents{
					${objectToCssString(contentStyle)}
				}`}
				{`#${blockProps.id} .product-price{
					${objectToCssString(priceStyle)}
				}`}
				{`#${blockProps.id} .wp-element-button,
				#${blockProps.id} .wp-element-button:hover{
					${objectToCssString(buttonStyle)}
					border-width: 0;
				}`}
			</style>
			<BlockControls>
				<AlignmentToolbar
					value={alignment}
					onChange={(alignment) => setAttributes({ alignment })}
				/>
			</BlockControls>
			<InspectorPanel {...props} />
			<span {...blockProps}>
				<ServerSideRender
					attributes={{ layout }}
					className={`wc-booster-wishlist-page ${prefix}-align-${alignment}`}
					block="wc-booster/wish-list-table"
					httpMethod="POST"
					LoadingResponsePlaceholder={Spinner}
				/>
			</span>
		</>
	);
};

export default Edit;
