/*----------------------------------------------------------------------------------------------------------------------------*
 *                       									                                                                  *
 * Register Wish List						                                                                              *
 *          									                                                                              *
 * @link https://wordpress.org/gutenberg/handbook/designers-developers/developers/tutorials/block-tutorial/writing-your-      *
 *       first-block-type/  																								  *
 * @since 1.0.0								   																                  *
 *															   																  *
 *----------------------------------------------------------------------------------------------------------------------------*/

import { register } from "../../helpers";
import metadata from "./block.json";
import edit from "./components/edit";
import "./styles/style.scss";

/*----------------------------------------------------------------------------------------------------------------------------*
 *																															  *
 * Get Object for Registering Block                                                                                           *
 *                                                                                                                            *
 * @see https://developer.wordpress.org/resource/dashicons/                                                                   *
 * @see https://wordpress.org/gutenberg/handbook/designers-developers/developers/block-api/block-registration/#icon-optional  *
 * for dashicons                          																					  *
 * @since 1.0.0                                                                                                               *
 *                                                                                                                            *
 *----------------------------------------------------------------------------------------------------------------------------*/

register(metadata.name, {
	edit,
	icon: (
		<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
			<path d="M64 256V160H224v96H64zm0 64H224v96H64V320zm224 96V320H448v96H288zM448 256H288V160H448v96zM64 32C28.7 32 0 60.7 0 96V416c0 35.3 28.7 64 64 64H448c35.3 0 64-28.7 64-64V96c0-35.3-28.7-64-64-64H64z" />
		</svg>
	),
	save() {
		return null;
	},
});

document.addEventListener("DOMContentLoaded", function () {
	const { registerCheckoutFilters } = window.wc.blocksCheckout;

	const modifyAdditionalInnerBlockTypes = (
		defaultValue,
		extensions,
		args,
		validation,
	) => {
		defaultValue.push("wc-booster/wish-list-table");
		return defaultValue;
	};

	registerCheckoutFilters("example-extension", {
		additionalCartCheckoutInnerBlockTypes: modifyAdditionalInnerBlockTypes,
	});
});
