import { 
    PanelBody,
    __experimentalToggleGroupControl as ToggleGroupControl,
    __experimentalToggleGroupControlOption as ToggleGroupControlOption,
 } from "@wordpress/components";
 import { PanelColor } from "../../../custom-control";
import Typography from "../../../custom-control/typography";
import { InspectorControls } from "@wordpress/block-editor";
import Dimension from "../../../custom-control/dimension";
import { __ } from "@wordpress/i18n";

import metadata from "../block.json";

import { getDefaultAttr, isUndefined } from "../../../helpers";

const InspectorPanel = (props) => {
	const {
		attributes: {
			layout,
			titleTypo,
			priceTypo,
			buttonTypo,
			buttonPadding,
			contentPadding,
			titlePadding,
			padding,
			margin,
			bgColor,
			removeColor,
			removeBGColor,
			buttonBGColor,
			buttonColor
		},
		setAttributes,
	} = props;

	const def = getDefaultAttr(metadata);
	return (
		<>
			<InspectorControls>
				<PanelBody title={__("General", "wc-booster")} initialOpen={false}>
                <ToggleGroupControl
                    value={layout} 
                    onChange={(layout) => setAttributes({ layout })}
                    isBlock
                    >
                    <ToggleGroupControlOption value="table" label={__("Table", "wc-booster")} />
                    <ToggleGroupControlOption value="grid" label={__("Grid", "wc-booster")} />
                    <ToggleGroupControlOption value="list" label={__("List", "wc-booster")} />
                </ToggleGroupControl>

				</PanelBody>
				<PanelBody title={__("Color", "wc-booster")} initialOpen={false}>
				<PanelColor
					colorSettings={[
						{
							label: __( "Button Background" ),
							value: buttonBGColor,
							onChange: (buttonBGColor) => setAttributes({ buttonBGColor: isUndefined( buttonBGColor ) ? '' : buttonBGColor }),
						},
						{
							label: __( "Button Color" ),
							value: buttonColor,
							onChange: (buttonColor) => setAttributes({ buttonColor }),
						},
						...(layout === "grid" ? [{
							label: __("Product Background"),
							value: bgColor,
							onChange: (bgColor) => setAttributes({ bgColor }),
						}] : []),
						...(layout === "grid" ? [{
							label: __( "Remove Icon Color" ),
							value: removeColor,
							onChange: (removeColor) => setAttributes({ removeColor }),
						}] : []),
						...(layout === "grid" ? [{
							label: __( "Remove Icon Background" ),
							value: removeBGColor,
							onChange: (removeBGColor) => setAttributes({ removeBGColor }),
						}] : [])
					]}
				/>
				</PanelBody>
				
				<PanelBody title={__("Typography", "wc-booster")} initialOpen={false}>
					<Typography
						label={__("Title", "wc-booster")}
						value={titleTypo}
						defaultValue={def("titleTypo")}
						onChange={(titleTypo) => setAttributes({ titleTypo })}
					/>
					<Typography
						label={__("Price", "wc-booster")}
						value={priceTypo}
						defaultValue={metadata.attributes.priceTypo.default}
						onChange={(priceTypo) => setAttributes({ priceTypo })}
					/>
					<Typography
						label={__("Button", "wc-booster")}
						value={buttonTypo}
						defaultValue={metadata.attributes.buttonTypo.default}
						onChange={(buttonTypo) => setAttributes({ buttonTypo })}
					/>
				</PanelBody>
				<PanelBody title={__("Spacing", "wc-booster")} initialOpen={false}>
					<Dimension
						label={__("Content Padding", "wc-booster")}
						value={contentPadding}
						defaultValue={metadata.attributes.contentPadding.default}
						onChange={(contentPadding) => setAttributes({ contentPadding })}
					/>
					<Dimension
						label={__("Title Padding", "wc-booster")}
						value={titlePadding}
						defaultValue={metadata.attributes.titlePadding.default}
						onChange={(titlePadding) => setAttributes({ titlePadding })}
					/>
					<Dimension
						label={__("Button Padding", "wc-booster")}
						value={buttonPadding}
						defaultValue={metadata.attributes.buttonPadding.default}
						onChange={(buttonPadding) => setAttributes({ buttonPadding })}
					/>
					<Dimension
						label={__("Padding", "wc-booster")}
						value={padding}
						defaultValue={metadata.attributes.padding.default}
						onChange={(padding) => setAttributes({ padding })}
					/>
					<Dimension
						label={__("Margin", "wc-booster")}
						value={margin}
						defaultValue={metadata.attributes.margin.default}
						onChange={(margin) => setAttributes({ margin })}
					/>
				</PanelBody>
			</InspectorControls>
		</>
	);
};

export default InspectorPanel;
